	/**
	 *  
	 * @param args  Array  []
	 * @returns 
	 */
	export function mergeArray(...args: any[]) {
		let params = new Array();
		let objectNum = 0;
		let formList = [];
		[].push.apply(formList, args)
		if (formList.length > 0) {
			formList.forEach((item, index) => {
				if (item instanceof Array) {
					objectNum = objectNum + 1;
					for (let i = 0; i < item.length; i++) {
						let value = item[i];
						params.push(value);
					}
				}
			})
		}
		if (objectNum == 0) {
			return params
		}
		return params
	}

	/**
	 *  
	 * @param args  Object  {}
	 * @returns 
	 */
	export function mergeObject(...args: any[]) {
		let params = {}
		let objectNum = 0;
		let formList = [];
		[].push.apply(formList, args)
		if (formList.length > 0) {
			formList.forEach((item, index) => {
				if (item && typeof (item) == "object") {
					objectNum = objectNum + 1
					for (let key in item) {
						if (Object.prototype.hasOwnProperty.call(item, key)) {
							let value = item[key];
							if (value != null && params[key] != null) {//nullobject
								if (typeof (value) == "object" && typeof (params[key]) == "object") {
									value = mergeObject(params[key], value)
								}
							}
							params[key] = value
						}
					}
				}
			})
		}
		if (objectNum == 0) {
			return {}
		}
		return params
	}
