	//**number */  -15  4  = 1   -15  -4  = -3
	/**
	 * number
	 * @param a 
	 * @param b 
	 * @returns number
	 */
	export function number_mod(a: number, b: number): number {
		let c = Math.abs(a) % Math.abs(b)
		if (a < 0) {
			if (b < 0) {
				c = 0 - c;
			} else {
				if (c != 0) {
					c = 0 - c;
					c = c + b;
				}

			}
		} else {
			if (b < 0) {
				if (c != 0) {
					c = 0 - c;
					c = c + Math.abs(b);
					c = 0 - c;
				}
			}
		}
		return c;
	}
