	/** HTTP */
	export function string_isHttp(param: string): boolean {
		let res = string_isEmpty(param);
		if (res) {
			return false;
		}
		let str = String(param).toLowerCase();
		if (str.search("http://") > -1) {
			return true;
		} else if (str.search("https://") > -1) {
			return true;
		}
		return false
	}


